@import "../../../SASS/Compatibilites";
.impagination{
    button{
        @include borderRadius(5px);
        padding: 2px 5px;
        background-color: transparent;
        border: 1px solid #60aeff;
        &:hover{
            background-color: #59d7f3;
        }
    }
    #prev{
        margin-right: 10px;
    }
    #next{
        margin-left: 10px;
    }
    margin-bottom: .5rem;
}