@import "../../../SASS/Compatibilites";
@import "../../../SASS/Position";
.job-section{
   width: 100%; 
   height: 100%;
   position: relative;
   .jobs-list{
       width: 100%;
       list-style: none;
       margin: 0;
       padding: 0;
       li{
           width: 100%;
           @include borderRadius(5px);
           padding: .5rem 1rem;
           border: 2px solid #60aeff;
           margin-bottom: .5rem;
           white-space: nowrap;
           overflow-x: hidden;
           text-overflow: ellipsis;
           h6{
               margin: 0;
               font-weight: 700;
           }
           span{
               font-size: 0.8rem;
               color: rgba(0, 0, 0, .5);
               span{
                   color: black;
                   font-weight:600;
               }
           }
           p{
               font-size: 0.6rem;
               color: rgba(0, 0, 0, .8);
               position: relative;
               i{
                   position: absolute;
                   right: 0;
                   font-size: 1rem
               }
               .fas{
                   color: red;
               }
           }
   
       }
   }
}